@use '../variables' as *;

/*****************
    Footer Section
******************/
.footer {
    background-color: $white;
    border-top: 1px solid $grey-300;
    padding-top: 1em;
    padding-bottom: 1em;
    margin-top: auto;

    &-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}