/*****************
    Variables
******************/
:root {
  --hue: 210;
  --sec-hue: 37;
}

/*****************
    Base
******************/
/* Reset */
html {
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  margin: auto;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex-grow: 1;
}

ul,
ol {
  padding-inline-start: 0;
}

/* Helpers - Visibility */
[hidden] {
  display: none !important;
}

/* Helpers - Container */
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
/* Helpers - Text */
.text-upper {
  text-transform: uppercase;
  letter-spacing: -0.25px;
}

.text-center {
  text-align: center !important;
}

/* Helpers - Padding */
.section-header,
.py-2 {
  padding-top: 2em;
  padding-bottom: 2em;
}

.section-header {
  background-color: #f6f6f7;
}

/* Helpers - Content center */
.content-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Helpers - CK Editor */
#editor,
.ck-editor {
  max-width: 800px;
}

/*****************
    Buttons
******************/
.btn, .btn-link, .btn-outline-primary, .btn-primary {
  appearance: none;
  background-color: transparent;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.25em;
  border: 2px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
  margin-right: 0.5em;
  padding: 0.45em 0.75em;
  text-decoration: none;
  transition: background-color 200ms ease-in;
}
.btn:focus, .btn-link:focus, .btn-outline-primary:focus, .btn-primary:focus, .btn:focus-visible, .btn-link:focus-visible, .btn-outline-primary:focus-visible, .btn-primary:focus-visible {
  box-shadow: 0 0 0 4px hsl(var(--hue), 80%, 85%);
}
.btn-primary {
  background-color: hsl(var(--hue), 80%, 30%);
  color: #fff;
}
.btn-primary:hover {
  background-color: hsl(var(--hue), 92%, 35%);
}
.btn-outline-primary {
  background-color: #fff;
  border-color: hsl(var(--hue), 80%, 30%);
  color: hsl(var(--hue), 80%, 30%);
}
.btn-outline-primary:hover {
  background-color: hsl(var(--hue), 80%, 93%);
}
.btn-link {
  text-decoration: underline 1px solid hsl(var(--hue), 80%, 30%);
  color: hsl(var(--hue), 80%, 30%);
  padding: 0;
}
.btn-lg {
  font-size: 1.125rem;
}
.btn-stretch {
  flex-grow: 1;
  font-size: clamp(1.0625rem, 2vw, 1.25rem);
  margin-right: 0;
}

/*****************
    Images
******************/
img,
picture {
  display: block;
  max-width: 100%;
}

figure {
  margin: 2em 0;
}
figure img {
  border-radius: 0.325em;
  margin-bottom: 0.25em;
}

figcaption {
  color: #666;
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
}

/*****************
    Alerts
******************/
.alert, .alert-info {
  border: 1px solid transparent;
  border-radius: 6px;
  display: flex;
  align-items: center;
  gap: 0.75em;
  margin-bottom: 0.75em;
  padding: 0.75em 1em;
}
.alert > svg, .alert-info > svg {
  align-self: flex-start;
}
.alert > p, .alert-info > p {
  font-size: 0.875rem;
  margin-bottom: 0;
}
.alert-info {
  background-color: rgb(231, 247, 253);
  color: rgb(2, 158, 242);
}

/*****************
    Avatars
******************/
.avatar {
  background-color: #555;
  border-radius: 50%;
  color: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  margin-right: 0.5em;
  height: 42px;
  width: 42px;
  user-select: none;
  overflow: hidden;
}

/*****************
    Card
******************/
.card {
  background-color: #fff;
  border: 1.5px solid #e2e2e7;
  border-radius: 0.5em;
  padding: 1.5em;
  margin: 0 1em 1em 0;
  width: 100%;
}

/*****************
    Modal
******************/
.modal {
  display: none;
  position: fixed;
}
.modal::before {
  position: fixed;
  content: "";
  inset: 0;
  background-color: #000;
  opacity: 0.7;
}
.modal.show {
  display: block;
}
.modal__container {
  background-color: #fff;
  border-radius: 1em;
  border: 2px solid #666;
  min-width: 300px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1.5em;
}
.modal h3 {
  margin-bottom: 1em;
}
.modal button {
  display: block;
  margin-left: auto;
}

/*****************
    Typography
******************/
/* Heading Styles */
h1,
h2,
h3,
h4 {
  font-weight: 500;
  margin-bottom: 0.3em;
  line-height: 1;
}

:is(h1, h2, h3) span {
  color: hsl(var(--hue), 80%, 30%);
}

h1,
h2 {
  font-family: "Oswald", serif;
}

h1 {
  font-size: clamp(2rem, 4vw, 2.75rem);
}

h2 {
  font-size: clamp(1.75rem, 3vw, 2rem);
}

h3 {
  font-size: clamp(1.375rem, 2vw, 1.625rem);
  font-weight: 600;
}

/* Link Styles */
a {
  font-size: inherit;
  color: #0066dd;
  text-decoration: underline 2px transparent;
  transition: text-decoration-color 200ms ease-in-out;
}
a:focus, a:focus-visible {
  outline-color: hsl(var(--hue), 80%, 30%);
}
a:hover {
  text-decoration-color: #0066dd;
}

/* Paragraph Styles */
p {
  color: #45454f;
  font-size: 1rem;
  line-height: 1.4;
  margin-bottom: 1em;
}

small {
  font-size: 0.75rem;
}

/* Extra font classes */
.fs-small {
  font-size: 0.75rem;
}

/*****************
    Top bar Section
******************/
.topbar {
  background-color: #e2e2e7;
  font-size: 0.75rem;
  text-align: center;
  padding: 10px;
}

/*****************
    Logo
******************/
.logo {
  letter-spacing: 0.25px;
  text-transform: uppercase;
  font-weight: 500;
}
.logo a {
  color: #000;
}
.logo span {
  color: #402BCA;
}

/*****************
    Navbar Section
******************/
.navbar {
  border-bottom: 1px solid #e2e2e7;
  padding-top: 12px;
  padding-bottom: 12px;
}
.navbar-container {
  flex-wrap: wrap;
}
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.75em;
}
.navbar-list, .navbar-login-list {
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 1em;
}
.navbar-list {
  margin-left: auto;
}
.navbar .nav-link {
  color: #000;
  font-weight: 500;
  font-size: 1.125rem;
}

/*****************
    Footer Section
******************/
.footer {
  background-color: #fff;
  border-top: 1px solid #ccc;
  padding-top: 1em;
  padding-bottom: 1em;
  margin-top: auto;
}
.footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Default form controls */
input,
textarea {
  background-color: #fff;
  border: none;
  color: #000;
  font-family: inherit;
  font-size: 1rem;
  outline: 1.75px solid #e2e2e7;
  padding: 0.75em 1em;
  width: 100%;
  transition: outline-color 200ms ease, box-shadow 200ms ease;
}
input:hover,
textarea:hover {
  outline-color: hsl(var(--hue), 80%, 75%);
}
input:focus,
textarea:focus {
  outline-color: hsl(var(--hue), 80%, 30%);
  box-shadow: 0 0 0 5px hsl(var(--hue), 80%, 85%);
}

/*****************
    Form Layout
******************/
.form {
  max-width: 380px;
  margin: 1em auto;
  width: 100%;
}
.form-heading {
  text-align: center;
  margin-bottom: 0.5em;
}
.form-control {
  display: flex;
  flex-direction: column;
  gap: 0.125em;
  margin-bottom: 1.5em;
  width: 100%;
}
.form-control:first-of-type {
  margin-top: 2em;
}
.form label {
  color: #45454f;
  cursor: pointer;
  font-size: 1rem;
  margin-bottom: 0.25em;
}
.form input[type=text],
.form input[type=tel],
.form input[type=email],
.form input[type=password],
.form textarea {
  border-radius: 0.325em;
}
.form .input-error {
  color: red;
  margin-bottom: 0;
  text-align: end;
}
.form-buttons {
  display: flex;
  justify-content: end;
}

/*****************
    Hero Section
******************/
.hero {
  background-color: #f6f6f7;
}
.hero-container {
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 2em;
  padding: 1em 0;
}
@media (max-width: 578px) {
  .hero-container {
    flex-direction: column;
    padding-top: 100px;
  }
}
.hero-content {
  align-self: center;
  flex-grow: 1;
}
.hero-content small {
  display: block;
  margin-bottom: 0.75em;
  text-transform: uppercase;
}
.hero-content h1 {
  margin-bottom: 0.25em;
}
.hero-content p {
  font-weight: 500;
}
.hero-cta .btn-primary {
  border-radius: 1em;
}
.hero-image {
  align-self: center;
  flex: 1 1 300px;
  display: flex;
  align-items: center;
  max-width: 500px;
  height: 360px;
}
.hero-image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: top left;
}

/*****************
    Month Read Section
******************/
.monthread {
  gap: 1em;
  width: fit-content;
}
.monthread-card {
  background-color: #fff;
  display: flex;
  align-items: start;
  gap: 1.5em;
  border: 1px solid #666;
  border-radius: 1em;
  margin-bottom: 1em;
  max-width: 600px;
  overflow: hidden;
}
@media (max-width: 578px) {
  .monthread-card {
    flex-direction: column-reverse;
  }
}
.monthread-content {
  align-self: center;
  flex-grow: 1;
  padding: 1em 1em 1em 1.25em;
}
.monthread-content small {
  display: block;
  margin-bottom: 0.75em;
  text-transform: uppercase;
}
.monthread-content h2 {
  margin-bottom: 0.25em;
}
.monthread-content p:first-of-type {
  font-weight: 500;
  margin-bottom: 0.5em;
}
.monthread-content em {
  font-family: serif;
  font-weight: 400;
}
.monthread-image {
  align-self: stretch;
  flex: 1 1 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  background-image: radial-gradient(#666 1px, #fff 1px);
  background-size: 18px 18px;
  height: 300px;
}
.monthread-image img {
  opacity: 1;
}
.monthread-sugesstioncard {
  max-width: 600px;
  background-color: #fff;
  background: repeating-linear-gradient(-45deg, #f2f2f4, #f2f2f4 3px, #fff 4px, #fff 30px);
}

/*****************
    CTA Section
******************/
.indexcta {
  background-color: #f6f6f7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
}

/* Dashboard Styles */
/*****************
    Index Page
******************/
.dash h2 {
  font-family: "Inter", -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  letter-spacing: -1px;
}
.dash-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.dash-cards > .card {
  flex: 1 1 300px;
}
.dash-books {
  background-color: #fff;
  background-image: repeating-radial-gradient(circle at 0 0, transparent 0, #ffffff 20px), repeating-linear-gradient(rgba(245, 245, 247, 0.3333333333), #f2f2f4);
}
.dash-reviews {
  background-color: #ffffff;
  background: radial-gradient(circle, transparent 20%, #ffffff 20%, #ffffff 80%, transparent 80%, transparent), radial-gradient(circle, transparent 20%, #ffffff 20%, #ffffff 80%, transparent 80%, transparent) 17.5px 17.5px, linear-gradient(#f2f2f4 1.5px, transparent 1.5px) 0 -0.7px, linear-gradient(90deg, #f2f2f4 1.5px, #ffffff 1.5px) -0.7px 0;
  background-size: 35px 35px, 35px 35px, 17.5px 17.5px, 17.5px 17.5px;
}
.dash-notes {
  background-color: #fff;
  background-image: radial-gradient(#e2e2e7 1px, #fff 1px);
  background-size: 18px 18px;
}
.dash-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
}
.dash-account {
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dash-account-image {
  border: 1px solid #e2e2e7;
  border-radius: 50%;
  overflow: hidden;
  width: 60px;
  height: 60px;
}
.dash ul {
  padding-left: 1em;
}
.dash li {
  line-height: 1.5;
}

/* Blog Page Styles */
/*****************
    Blog Section
******************/
.blog {
  margin: 2em 0 4em;
}
.blog__post-list {
  list-style: none;
  padding: 0;
}
.blog__post-item {
  display: flex;
  border-bottom: 2px solid #e2e2e7;
  gap: 0.5em;
  padding: 1.5em 0;
}
@media (min-width: 600px) {
  .blog__post-item {
    padding-left: 2em;
    padding-right: 2em;
  }
}
.blog__post-item__content {
  display: grid;
  flex: 0 1 76%;
}
.blog__post-item__content h3 {
  font-size: clamp(1.375rem, 2vw, 1.625rem);
  margin-bottom: 0;
  margin-top: 0.25em;
  letter-spacing: 0;
  line-height: 1.2;
}
.blog__post-item__content h3 a {
  color: #000;
}
.blog__post-item__content h3 a:hover {
  text-decoration-color: transparent;
  color: hsl(var(--hue), 80%, 30%);
}
.blog__post-item__content p {
  display: none;
}
@media (min-width: 600px) {
  .blog__post-item__content p {
    display: block;
  }
}
.blog__post-item__date {
  color: #666;
}
.blog__post-item__meta {
  align-self: baseline;
  margin-top: 0.5em;
}
.blog__post-item__meta span {
  color: #45454f;
  font-size: 0.75rem;
}
.blog__post-item__meta span:first-of-type {
  background-color: #f6f6f7;
  border-radius: 1.5em;
  font-weight: 500;
  letter-spacing: -0.25px;
  margin-right: 0.5em;
  padding: 0.5em 1em;
}
.blog__post-item__image {
  border-radius: 0.325em;
  overflow: hidden;
  width: 120px;
  height: 120px;
}
.blog__post-item__image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
@media (min-width: 600px) {
  .blog__post-item__image {
    width: 220px;
    height: 180px;
  }
}
.blog__tags {
  border-bottom: 1px solid #f2f2f4;
  border-radius: 0.325em;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.125em;
  background-color: #f6f6f7;
  padding: 1.25em 1.5em 0.875em;
}
.blog__tags > span {
  text-transform: uppercase;
  letter-spacing: 2px;
}
.blog__tags-list {
  list-style: none;
  display: flex;
  align-items: center;
  column-gap: 0.5em;
  padding: 0;
  min-height: 54px;
  overflow-x: auto;
  overflow-y: hidden;
}
.blog__tags-list li:first-of-type a {
  display: flex;
  align-items: center;
  gap: 0.125em;
}
.blog__tags-list li:first-of-type a span {
  color: hsl(var(--hue), 80%, 30%);
  font-size: 1.125rem;
}
.blog__tags-item-link {
  background-color: #fff;
  border: 2px solid #e2e2e7;
  border-radius: 0.325em;
  color: #45454f;
  font-size: 0.875rem;
  font-weight: 500;
  padding: 0.5em 0.75em;
  overflow: hidden;
  white-space: nowrap;
}
.blog__tags-item-link.current {
  background-color: hsl(var(--hue), 80%, 98%);
  border-color: hsl(var(--hue), 80%, 30%);
}
.blog__tags-item-link:hover {
  border-color: hsl(var(--hue), 80%, 30%);
  text-decoration-color: transparent;
}

/* Single Page Styles */
/*****************
    Signup/Login pages
******************/
main#signup-page,
main#login-page {
  width: 100%;
  background-color: #fff;
  background-image: radial-gradient(#ccc 1.3px, transparent 1.3px), radial-gradient(#ccc 1.3px, #fff 1.3px);
  background-size: 52px 52px;
  background-position: 0 0, 26px 26px;
}

/*****************
    Error page
******************/
.error-page {
  background-color: #f6f6f7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60vh;
}
.error-page-container {
  max-width: 600px;
}
.error-page h2 {
  margin-bottom: 0.5em;
}
.error-page p {
  color: #45454f;
  margin-bottom: 1em;
}